import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'config_sistema-dados',   templateUrl: 'config_sistema_dados.component.html' }) export class Config_sistemaDadosComponent implements OnInit {       displayMode: Config_sistemaDadosDisplayModeEnum;     displayModeEnum = Config_sistemaDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Config_sistemaDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Config_sistemaDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Config_sistemaDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Config_sistemaDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


