import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Config_sistema } from '../../../modelo/Config_sistema'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_sistema-dados-details',   templateUrl: 'config_sistema_dadosDetails.component.html' }) export class Config_sistemaDadosDetailsComponent implements OnInit {    config_sistema: Config_sistema;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.config_sistema.Config_sistema";                 let id = +params['id'];         this.config_sistema = new Config_sistema();         this.config_sistema.cd_config_sistema = id;         o.objetoJson = this.config_sistema;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a config_sistema. '; }      this.config_sistema = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


